.home {
  // TEMPORARY BORDER
  // border: solid 1px purple;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;

  
}
.Intro {
  // border: solid 1px red;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FAFAFA;

  .intro-text {
    width: 100vw;
    position: fixed;
    will-change: transform;
    // display: flex;
    // align-items: center;
    .Name {
      font-family: 'lemonde-journal', serif;
      font-weight: 600;
      text-align: center;
      font-size: 72px;
      margin-top: 40vh;
      cursor: default;
      z-index: 0;
    }
  
    .Greeting {
      margin-top: 30px;
      top: 20vh;
      height: fit-content;
      text-align: center;
      word-spacing: 1.6px;
    }
  }
}
#intro-ends {
  content:'';
}

.about-section {
  width: 100%;
  height: fit-content;
  .about-inner {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    height: fit-content;
    .about-component {
      z-index: 3;
      height: fit-content;
      padding-bottom: 10vh;
    }
    .angle-border {
      z-index: 2;
      content:'';
      position: absolute;
      top: -150px;
      left: 0;
      width: 100%;
      height: calc(100% + 40vh);
      background-color: #ffffff;
      transform: skewY(4deg);
    }
  }
}
#about-ends {
  content:'';
}

.publications-section {
  z-index: 4;
}

.projects-section {
  position: relative;
  width: 100%;
  height: fit-content;
  display: flex;
  background-color: white;
  .projects-component {
    z-index: 5;
    height: fit-content;
  }
  .angle-border {
    z-index: 4;
    content:'';
    position: absolute;
    top: -150px;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    transform: skewY(4deg);
  }
}

// I like this color
// color: #5186f0;


@media screen and (max-width: 1080px) {
  .Intro {
    margin-bottom: 4.132231404958677vw;
    .Name {
      font-size: 11.8vw;
    }
  }
  
}

@media screen and (max-width: 720px) {
  .Intro {
    align-items: center;
    .intro-text {
      .Name {
        font-size: 14vw;
      }
    }
  }
}
